/* #Reset default styles - start
   ========================================================================== */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font,
ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    background: transparent;
    border: 0;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    outline: none;
}

article, aside, details, figcaption, figure, footer, header, nav, section {
    display: block;
}

del {
    text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
    border: medium none;
    vertical-align: middle;
}

table th {
    border: 0;
    padding: 5px 7px;
    text-align: center;
}

table td {
    padding: 5px 10px;
    text-align: center;
}

a img {
    border: none;
}

img, .wp-caption {
    max-width: 100%;
    height: auto;
}

iframe {
    max-width: 100%;
}

/* #Reset default styles - end
   ========================================================================== */

/* #General styles that are used in our theme - start
 * Here are defined global styles on body element, on container elements that hold every page,
 * headings and links, inputs, core wp styles and default pagination styles
   ========================================================================== */

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

.clearfix {
    *zoom: 1;
}

.clear:after {
    clear: both;
    content: "";
    display: block;
}

html {
    overflow-y: scroll !important; /* for vertical split screen slider shortcode on touch devices */
}

body {
    font-family: $default-text-font;
    font-size: 16px;
    line-height: 26px;
    font-weight: 300;
    letter-spacing: 0.05em;
    color: $default-text-color;
    background-color: $default-background-color;
    overflow-x: hidden !important;
    -webkit-font-smoothing: antialiased;
}

h1 {
    font-size: 52px;
    line-height: 1.17em;
    letter-spacing: 0.1em;
}

h2 {
    font-size: 40px;
    line-height: 1.11em;
    letter-spacing: 0.1em;
}

h3 {
    font-size: 26px;
    line-height: 1.23em;
    letter-spacing: 0.15em;
}

h4 {
    font-size: 18px;
    line-height: 1.47em;
    letter-spacing: 0.25em;
}

h5 {
    font-size: 25px;
    line-height: 1.32em;
    letter-spacing: 0.2em;
}

h6 {
    font-size: 25px;
    line-height: 1.6em;
    letter-spacing: 0.05em;
}

h1, h2, h3, h4 {
    font-family: $first-heading-font;
    font-weight: 700;
    text-transform: uppercase;
}

h5, h6 {
   font-family: $second-heading-font; 
   font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
    margin: 25px 0;
    color: $default-heading-color;
    
    a {
        @include mkdTypographyLayout();
    }
}

a,
p a {
    color: $default-heading-color;
    text-decoration: none;
    @include mkdTransition(color .2s ease-out);
}

p {
    margin: 10px 0;
}

input[type='submit'] {
    -webkit-appearance: none;
}

ul, ol {
    list-style-position: inside;
    margin: 15px 0;
}

ul ul,
ol ol,
ol ul,
ul ol {
    margin: 0;
    padding: 0 0 0 15px;
}

pre {
    background-color: $default-background-color;
    padding: 15px;
    max-width: 100%;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 15px 0;
}

dt {
    font-size: 15px;
    font-weight: bold;
    margin-bottom: 10px;
}

dd {
    margin-bottom: 15px;
}

sub, sup {
    font-size: 75%;
    height: 0;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sub {
    top: 0.5ex;
}

sup {
    bottom: 0.5ex;
}

.wp-caption-text {
    margin: 0;
    font-style: italic;
}

table {
    width: 100%;
}

table thead tr, table tbody tr {
    border: 1px solid $default-border-color;
}

table tbody th {
    border-right: 1px solid $default-border-color;
}

blockquote {
    @include mkdRelativeHolderLayout();
    margin: 35px 0;
    padding: 0px;
    font-size: 25px;
    font-family: $second-heading-font;
    color: $default-heading-color;
    line-height: 40px;
    text-align: left;
    quotes: none;
    box-sizing: border-box;

    > * {
        @include mkdTypographyLayout();
        margin: 0;
        padding: 60px 9% 64px;

        &:before {
            content:"\201D";
            color:$first-main-color;
            font-size: 120px;
            font-family: Bellefair,serif;
            display:block;
            position:absolute;
            top: 24px;
            left: -7px;
        }
    }

    &:before,
    &:after {
        content: '';
        background-color: $default-border-color;
        width: 100%;
        height: 1px;
        display: block;
    }

    &:before {
        width: 91%;
        float: right;
        padding-right: 9%;
        box-sizing: border-box;
    }

	cite {
		font-style: normal;
		font-weight: 400;
	}
}

/* Webkit */
::selection {
    background: $first-main-color;
    color: $default-heading-color;
}

/* Gecko/Mozilla */
::-moz-selection {
    background: $first-main-color;
    color: $default-heading-color;
}

.wp-caption,
.wp-caption-text,
.sticky,
.gallery-caption,
.bypostauthor {
    opacity: 1;
}

.alignleft {
    display: inline;
    float: left;
    margin: 0 20px 20px 0;
}

.alignright {
    display: inline;
    float: right;
    margin: 0 0 20px 20px;
}

.aligncenter {
    clear: both;
    display: block;
    margin: 10px auto;
}

.alignwide {
    margin-left: auto;
    margin-right: auto;
    clear: both;
}

@include ipad-landscape {
    
    .alignwide {
        width: 100%;
        max-width: 100%;
    }
}

.alignfull {
    position: relative;
    left: -1rem;
    width: calc( 100% + (2 * 1rem));
    max-width: calc( 100% + (2 * 1rem));
    clear: both;
}

@include ipad-landscape {
    
    .alignfull {
        left: calc( -12.5% - 75px);
        width: calc( 125% + 150px);
        max-width: calc( 125% + 150px);
        margin-top: calc(2 * 1rem);
        margin-bottom: calc(2 * 1rem);
    }
}

.text-align-right {
    text-align: right;
}

.text-align-left {
    text-align: left;
}

.text-align-center {
    text-align: center;
}

.mkd-wrapper {
    position: relative;
    left: 0;
    z-index: 1000;

    .mkd-wrapper-inner {
        width: 100%;
        overflow: hidden;
    }
}

.mkd-content {
    position: relative;
    margin-top: 0;
    z-index: 100;
    background-color: $default-background-color; /*this is needed because of the uncovering footer*/
}

.mkd-boxed {
    overflow: hidden !important; /* this is because content jumping while using accordion and display:inline-block on 'body.mkd-boxed .mkd-wrapper'   */

    .mkd-wrapper {
        display: inline-block;
        vertical-align: middle;
        width: 100%;
    
        .touch & {
            background-repeat: repeat;
        }
        
        .mkd-wrapper-inner {
            width: $grid-width-boxed;
            overflow: visible;
            margin: auto;
        }
    }

    .mkd-content {
        overflow: hidden;
    }

    .mkd-footer-inner {
        width: $grid-width-boxed;
        margin: auto;
    }

    .mkd-page-header .mkd-sticky-header,
    .mkd-page-header .mkd-fixed-wrapper.fixed {
        width: $grid-width-boxed;
        left: auto;
    }
}

.mkd-paspartu-enabled {

    .mkd-wrapper {
        padding: 36px;
        background-color: #fff;
        box-sizing: border-box;

        &:after {
            content: '';
            display: block;
            position: fixed;
            width: 100%;
            height: 36px;
            background-color: #fff;
            left: 0;
            top: 0;
            z-index: 1001;
        }

        &:before {
            content: '';
            display: block;
            position: fixed;
            width: 100%;
            height: 36px;
            background-color: #fff;
            left: 0;
            bottom: 0;
            z-index: 1001;
        }
    }

    &.mkd-top-paspartu-disabled {

        .mkd-wrapper {
            padding-top: 0 !important;

            &:after {
                display: none;
            }
        }
    }
}

.mkd-smooth-transition-loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    background-color: #fff;
}

.mkd-container {
    position: relative;
    padding: 0;
    width: 100%;
    z-index: 100;

    &:before {
        content: " ";
        display: table;
    }

    &:after {
        content: " ";
        display: table;
        clear: both;
    }
}

.mkd-container-inner { //not in parent selector since this class is used for positioning other elements in grid
    width: $grid-width;
    margin: 0 auto;
}

.mkd-full-width {
    position: relative;
    z-index: 100;
}

.mkd-content .mkd-content-inner > .mkd-container > .mkd-container-inner,
.mkd-content .mkd-content-inner > .mkd-full-width > .mkd-full-width-inner {
	padding-top: 40px; // default value for content top padding
}

@media only screen and (min-width: 1400px) {

    .mkd-grid-1300 {

        .mkd-grid,
        .mkd-container-inner,
        .mkd-row-grid-section {
            width: $grid-width-1300;
        }
    }

    .mkd-grid-1300.mkd-boxed {

        .mkd-footer-inner,
        .mkd-wrapper-inner {
            width: $grid-width-1300 + 50px;
        }

        .mkd-page-header .mkd-sticky-header,
        .mkd-page-header .mkd-fixed-wrapper.fixed {
            width: $grid-width-1300 + 50px;
            left: auto;
        }
    }
}

