/* #General styles that are used in our theme - end
   ========================================================================== */

@include keyframes(preload-background) {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 100% 0;
    }
}

.mkd-preload-background {
    background: transparent url('../img/preload_pattern.png') repeat scroll inherit !important;
    @include animation(preload-background, 40s, infinite, linear, 0s);
}

.mkd-grid {
    width: $grid-width;
    margin: 0 auto;
}

.mkd-vertical-align-containers {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;

    &.mkd-25-50-25 {

        .mkd-position-left,
        .mkd-position-right {
            width: 25%;
        }

        .mkd-position-center {
            width: 50%;
            left: 50%;
            @include mkdTransform(translateX(-50%));
        }
    }

    &.mkd-33-33-33 {

        .mkd-position-left,
        .mkd-position-center,
        .mkd-position-right {
            width: 33.33%;
        }

        .mkd-position-center {
            width: 50%;
            left: 50%;
            @include mkdTransform(translateX(-50%));
        }
    }

    &.mkd-50-50 {

        .mkd-position-left,
        .mkd-position-right {
            width: 50%;
        }
    }

    &.mkd-33-66 {
        
        .mkd-position-left {
            width: 33.33%;
        }

        .mkd-position-right {
            width: 66.66%;
        }
    }

    &.mkd-66-33 {
        
        .mkd-position-left {
            width: 66.66%;
        }

        .mkd-position-right {
            width: 33.33%;
        }
    }

    .mkd-position-left {
        position: relative;
        float: left;
        z-index: 2;
        height: 100%;
    }
    
    .mkd-position-center {
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        text-align: center;
        z-index: 1;
    }

    .mkd-position-right {
        position: relative;
        float: right;
        z-index: 2;
        height: 100%;
        text-align: right;
    }

    .mkd-position-left-inner,
    .mkd-position-center-inner,
    .mkd-position-right-inner {
        vertical-align: middle;
        display: inline-block;
    }
    
    .mkd-position-right.mkd-has-widget .mkd-position-right-inner {
        height: 100%;
    }

    .mkd-position-left:before,
    .mkd-position-center:before,
    .mkd-position-right:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        height: 100%;
        margin-right: 0; /* adjusts for spacing before it was -3px */
    }
}

